import { useEffect, useState } from 'react';
import styles from './App.module.css'
import Web3 from 'web3';
import contractabi from './media/contractabi.json'

function App() {
  const [walletConnected, setwalletConnected] = useState(false)
  const [accounts, setaccounts] = useState([])
  const [web3instance, setweb3instance] = useState(null)
  const [contractInstance, setcontractInstance] = useState(null)
  const [flokiResult, setflokiResult] = useState(0)
  const [bnbValue, setbnbValue] = useState(0)
  async function connectWallet() {
    const web3 = new Web3();
    if (window.ethereum) {
      console.log("2s")
      web3.eth.setProvider(window.ethereum)
      await window.ethereum.enable();
      if (await web3.eth.net.getId() != 56) {
        await window.ethereum.request({
          method: 'wallet_addEthereumChain',
          params: [
            {
              chainId: `0x${(56).toString(16)}`,
              chainName: 'Binance Smart Chain Mainnet',
              nativeCurrency: {
                name: 'BNB',
                symbol: 'bnb',
                decimals: 18,
              },
              rpcUrls: ["https://bsc-dataseed.binance.org/"],
              blockExplorerUrls: ['https://bscscan.com/'],
            },
          ],
        })
      }
      setaccounts(await web3.eth.getAccounts())
      setcontractInstance(new web3.eth.Contract(
        contractabi,
        "0xa001c1e15abfdc66f82a7d57d19e864b083bb42e"
      ))
      setwalletConnected(true)
      setweb3instance(web3)
    }
  }

  function buy() {
    contractInstance.methods.buy().send({
      from: accounts[0],
      value: bnbValue
    })
  }

  function bnbChange(e){
    var newValue = e.target.value;
    if(!isNaN(newValue)){
      var bnb = web3instance.utils.toWei(newValue, 'ether')
      setbnbValue(bnb)
      var floki = web3instance.utils.fromWei(bnb, 'gwei')
      setflokiResult(floki*250)
    }
  }

  return (
    <div class={styles.contentcontainer}>
      <div class={styles.topbar}>
        <div class={styles.topbarleftalign}>
          <div class={styles.logo} />
          <p class={styles.logostaking}>Yellow Floki Private Sale</p>
        </div>
        {walletConnected ? <div class={`${styles.connectwalletbutton} ${styles.connectwalletbuttonactive}`}>Connected</div> :
          <div onClick={connectWallet} class={styles.connectwalletbutton}>Connect Wallet</div>}
      </div>
      {!walletConnected && <p class={styles.infotext}>
        Please connect wallet!
      </p>}
      {walletConnected &&
        <div class={styles.buyinputcontainer}>
          <div class={styles.buyinputelementcontainer}>
            <input onChange={bnbChange} class={styles.buyinputelement} />
            <p class={styles.buyinputlabel}>BNB</p>
          </div>
          <div class={styles.buyinputelementcontainer}>
            <input value={flokiResult} class={styles.buyinputelement} readOnly />
            <p class={`${styles.buyinputlabel} ${styles.purpleflokilabel}`}>YELLOWFLOKI</p>
          </div>
          <div onClick={buy} class={styles.buybutton}>BUY</div>
          <div className={styles.hang} />
        </div>
      }
    </div>
  );
}

export default App;
